import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { Message } from 'primeng/primeng';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { ProductManageHttpService } from '../../../../product-manage/http/product-manage.http.service';
@Component({
    selector: 'group-product',
    templateUrl: './group-product.component.html',
    styleUrls: ['./group-product.component.scss'],
    providers: [ProductManageHttpService, Commfunc]
})
export class GroupProductComponent implements OnInit {
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    @Input() inValue: any;

    //提示信息
    msgs: Message[] = [];
    // 关系类型
    relationType: any;
    code: any;

    relation_vale: string;//关系类型
    select_Tproduct: any = [];//关联可售产品
    iscompele: boolean = false;
    relationValue: any = [];//关联可售产品
    relationValued: string;//关联可售产品(chuan)
    is_choose_product2: any = [];//关联可售产品可选
    is_choose_product3: any = [];//关联可售产品可选(jia)
    valueListTwo: any = [];//关联可售产品已选
    relationPro_list: any = [];//产品数组
    constructor(
        public productPlatformHttpService: ProductManageHttpService,
        public commfunc: Commfunc,
        public httpService: CusOperationHttpService
    ) { }
    ngOnInit() {
        this.relationValue = [];
        this.product_queryg();
    }
    // 关联可售产品
    product_queryg() {
        let this_ = this;
        let delData2 = function (data) {
            let arr3 = [];
            for (let key of data.productLineList) {
                arr3.push({
                    label: key.productLineName,
                    value: [key.productLineId, 1],
                    children: [{}]
                })
                this_.select_Tproduct = arr3;
            }
        }
        this.interfaceComFnc(this.productPlatformHttpService.productCategoryLineQueryList({}), delData2);
    }
    interfaceComFnc(service_method, success_fnc) {
        service_method.subscribe(data => {
            if (data.returnCode != null && data.returnCode.code == "success") {
                if (success_fnc !== null) {
                    success_fnc(data);
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        });
    }
    // //关联
    // //-
    nodeCheckQg(event) {
        this.valueListTwo = [];
        let this_ = this;
        let parames = {
            pageNum: 1,
            pageSize: 10000,
            productTemplateId: event.node.value[0]
        }
        this.productPlatformHttpService.findAvailableProductList(parames).subscribe(data => {
            if (data.returnCode != null && data.returnCode.code == "success") {
                let arr = [];
                for (let key of data.resultList) {
                    arr.push({
                        label: key.productName,
                        value: [key.productId, 0],
                        children: [{}]
                    })
                }
                this_.is_choose_product2 = arr;
                this_.is_choose_product2.forEach((items, i) => {
                    this.relationValue.forEach(iten => {
                        if (items.value[0] === iten) {
                            let k = i.toString();
                            this.valueListTwo.push(k);
                        }
                    });
                });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        });
    }
    //+
    nodeExpendQg(event) {
        let this_ = this;
        let parames = {
            pageNum: 1,
            pageSize: 10000,
            productLineId: event.node.value[0]
        }
        let parames2 = {
            groupId: event.node.value[0]
        }
        let delData = function (data) {
            let arr = [];
            for (let key of data.productGroupInfoList) {
                arr.push({
                    label: key.groupName,
                    value: [key.groupId, 2],
                    children: [{}]
                })
            }
            event.node.children = arr;
        }
        let delData2 = function (data) {
            let arr = [];
            for (let key of data.groupTempList) {
                arr.push({
                    label: key.tplName,
                    value: [key.tplId, 3],
                })
            }
            event.node.children = arr;
        }
        if (event.node.value[1] === 1) {
            this.interfaceComFnc(this.productPlatformHttpService.productCategoryGroupInfoQueryList(parames), delData);
        } else if (event.node.value[1] === 2) {
            this.interfaceComFnc(this.productPlatformHttpService.pdGroupTemplateQuery(parames2), delData2);
        } else if (event.noed.value[1] === 3) {

        }
    }
    listTwo() {
        this.is_choose_product3 = this.is_choose_product2;
        this.valueListTwo.sort(function (a, b) { return b - a; });
        this.valueListTwo.forEach(item => {
            let n = 0;
            this.relationValue.forEach(items => {
                if (this.is_choose_product3[item].value[0] == items) {
                    n++;
                }
            });
            if (n === 0) {
                this.relationValue.push(this.is_choose_product3[item].value[0]);
                this.relationPro_list.push({
                    productCode: this.is_choose_product3[item].value[0],
                    productName: this.is_choose_product3[item].label
                })
            }
        });
        this.is_choose_product3.forEach((item, i) => {
            let k = 0;
            let m = 0;
            this.relationValue.forEach(items => {
                if (item.value[0] == items) {
                    k++;
                }
            });
            if (k > 0) {
                this.valueListTwo.forEach(itemd => {
                    if (i == itemd) {
                        m++;
                    }
                });
                if (m == 0) {
                    this.relationValue.forEach((itemsd, n) => {
                        if (item.value[0] == itemsd) {
                            this.relationValue.splice(n, 1);
                            this.relationPro_list.splice(n, 1);
                        }
                    });
                }
            }
        });
    }

    commit() {
        this.outValue.emit(this.relationPro_list);
    }
}

